import axios from "../../node_modules/axios/index";
var baseUri = "http://lgwebservice.azurewebsites.net/api/starttime";
var baseUrlForWeather = "http://api.openweathermap.org/data/2.5/weather?q=roskilde&APPID=1a38fe1f59581dcfa8c2121a26de6b86";
var videoLogs = document.getElementById("videoLogs");
var contentForCleanTimer = document.getElementById("cleanTimer");
var contentOfTimerMad = document.getElementById("feedTimer");
var contentTemp = document.getElementById("temp");
(function () {
    showAllTimeStamps();
    showWeather();
    TimerWithAlert(contentForCleanTimer, 345600000, "Tid til at skifte bur!");
    TimerWithAlert(contentOfTimerMad, 604800000, "Tid at give Pepsi mad og drikke");
    autoReloadData();
})();
function TimerWithAlert(divTag, milisecondsToRun, textToAlert) {
    // Set the date we're counting down to
    var countDownDate = new Date().getTime() + milisecondsToRun;
    var x = setInterval(function () {
        // Get today's date and time
        var now = new Date().getTime();
        var distance = countDownDate - now;
        var days = Math.floor(distance / (1000 * 60 * 60 * 24));
        var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        divTag.innerHTML = days + "d " + hours + "h " + minutes + "m";
        if (distance < 0) {
            clearInterval(x);
            (function () {
                alert(textToAlert);
                TimerWithAlert(divTag, milisecondsToRun, textToAlert);
            })();
        }
    }, 1000);
}
function autoReloadData() {
    var countDownDate = new Date().getTime() + 10000;
    var x = setInterval(function () {
        var now = new Date().getTime();
        var distance = countDownDate - now;
        if (distance < 0) {
            clearInterval(x);
            showAllTimeStamps();
            showWeather();
            autoReloadData();
        }
    }, 1000);
}
function showAllTimeStamps() {
    axios.get(baseUri)
        .then(function (response) {
        videoLogs.innerHTML = "";
        var result = "";
        response.data.forEach(function (record) {
            result += "#" + record.id + ": " + record.time + "<br>";
            videoLogs.innerHTML = result;
        });
    })
        .catch(function (error) {
        if (error.response) {
            videoLogs.innerHTML = error.message;
        }
        else {
            videoLogs.innerHTML = error.message;
        }
    });
}
function showWeather() {
    axios.get(baseUrlForWeather)
        .then(function (response) {
        var newData = response.data["main"]["temp"];
        contentTemp.innerHTML = String(newData - 273.15).slice(0, -14) + " grader c";
        console.log(response.data);
    })
        .catch(function (error) {
        if (error.response) {
            contentTemp.innerHTML = error.message;
        }
        else {
            contentTemp.innerHTML = error.message;
        }
    });
}
